import React from 'react'
import { get } from 'lodash'

export default class Dropdown extends React.Component {
  constructor(props) {
    super(props)
    const { lookIds } = props
    this.handleChange = this.handleChange.bind(this);
    this.state = { lookId: get(lookIds, 0) }
  }
  handleChange(e) {
    const { diceRoll } = this.props
    console.log(e.target.value)
    this.setState({...this.state, lookId: e.target.value})
    diceRoll(e)
  }
  
  dropdown() {
    const { lookIds } = this.props
    const style = {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-around',
      width: '110px',
    }

    return (
      <form style={style}>
        <label>Look ID</label>
        <select style={{fontSize:'14px'}} value={this.state.lookId} onChange={this.handleChange} name="Look ID">
          <option></option>
          {lookIds.map((lookId, i) => (
            <option value={lookId} key={i}>{lookId}</option>
          ))}
        </select>
      </form>
    )
  }

  render() {
    const { lookIds } = this.props
    return lookIds === undefined ? null : this.dropdown()
  }
}
